.fl {
  float: left;
}

.fr {
  float: right;
}

.transition {
  -webkit-transition: all .2s;
  transition: all .2s;
}
/*======================================================================
      Global
  ======================================================================*/

* {
  padding: 0;
  margin: 0;
  outline: none;
}

html {
  height: 100%;
  font-family: "microsoft yahei", Arial, "Helvetica Neue", Helvetica, sans-serif;
}

body {
  height: 100%;
  overflow: hidden;
  margin: 0  auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

p,
a,
button,
span {
  -webkit-font-smoothing: antialiased;
}

input {
  outline: none;
}

.pa {
  position: absolute;
}

/* psd width `divided` 10 */

/* 750/10=75 */

/* the class "content" is used for flexible plugin */

.content {
  display: block;
  min-width: 320px;
  margin: 0 auto;
  max-width: 750px;
}

/* swiper */

.swiper-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.swiper-slide {
  height: 100%;
  font-size: 58px;
  background: #ff9900;
}

/* scene */

.scene {
  -webkit-perspective: 3000;
  perspective: 3000;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* test code */

.scene {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.scene aside {
  -webkit-box-flex: 1;
  -webkit-flex: 1 1 auto;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  /* 300px width in psd,
     750px width total,
     auto responsive using flexible.js,
     set total width in $page-base-rem
  */
  width: 4rem;
  background: pink;
}

.scene main {
  -webkit-box-flex: 1;
  -webkit-flex: 1 1 auto;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  /* 450px width in psd,
     750px width total,
     auto responsive using flexible.js,
     set total width in $page-base-rem
  */
  width: 6rem;
  background: orange;
}

